// , 
import BigNumber from 'bignumber.js'


const basics = {
  // , 30
  // : ,,16(0x), (bug,); ;
  // : 
  basAdd(a, b) {
    a = new BigNumber(a);
    b = new BigNumber(b);
    const res = a.plus(b);
    // 
    return res.toFixed();
  },

  // 
  basSub(a, b) {
    a = new BigNumber(a);
    const res = a.minus(b);
    return res.toFixed();
  },

  // 
  basMul(a, b) {
    a = new BigNumber(a);
    const res = a.multipliedBy(b);
    return res.toFixed();
  },

  // 
  basDiv(a, b) {
    a = new BigNumber(a);
    const res = a.dividedBy(b);
    return res.toFixed();
  },

  // ; gte=, gt=, eq=, lt=, lte=
  // : ,
  // : 
  basGte(a, b) {
    a = new BigNumber(a);
    return a.gte(b)
  },
  basGt(a, b) {
    a = new BigNumber(a);
    return a.gt(b)
  },
  basEq(a, b) {
    a = new BigNumber(a);
    return a.eq(b)
  },
  basLt(a, b) {
    a = new BigNumber(a);
    return a.lt(b)
  },
  basLte(a, b) {
    a = new BigNumber(a);
    return a.lte(b)
  },

};

export default basics;



