// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import util from './common/util.js'
import contract from './common/contractECO_V2.js'
import 'element-ui/lib/theme-chalk/index.css'
import ElementUi from 'element-ui'
Vue.use(ElementUi);

/*
 :
 1. common/contractEco_v2.js abi, ;
 */

//  -- ECO
Vue.prototype.$onLaunched = new Promise(resolve => {
  Vue.prototype.$isResolve = resolve
});

// 
import VueI18n from 'vue-i18n'
Vue.use(VueI18n);
const i18n = new VueI18n({
  // 
  locale: localStorage.lang,
  messages: {
    EN: require('../static/lang/EN.json'),
    GER: require('../static/lang/GER.json'),
    IDN: require('../static/lang/IDN.json'),
    JP: require('../static/lang/JP.json'),
    KOR: require('../static/lang/KOR.json'),
    MYS: require('../static/lang/MYS.json'),
    VN: require('../static/lang/VN.json'),
    ZH: require('../static/lang/ZH.json'),
    HINDI:require('../static/lang/HINDI.json')
  }
});

// 
Vue.prototype.$bus = new Vue();
Vue.prototype.$util = util;
Vue.prototype.$contract = contract;
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  // 
  i18n,
  components: { App },
  template: '<App/>'
});



