<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		// 
		globalData: {
      // 
			gTronWeb: '',
			// 
			gMasterContract: '',
      // 
      gTicketsContract: '',
			// usdt
			gUSDTContract: '',
		},

		created() {
			window.addEventListener('message', function (e) {
			        if (e.data.message && e.data.message.action === 'setAccount') {
						// 
						console.log('===, ===', e);
            window.location.reload();
			        }
			    })
		},

		methods: {
			// 
			async ifTron() {
				// 
				let res = await this.$tron.isTronLink();
				// , , 
				if(!res.status) {
					console.log('');
					return;
				}
				// 
				this.globalData.gTronWeb = window.tronWeb;
				// usdt,,
				let masterAddr = this.$contract.masterAddress;
				let masterAbi = this.$contract.masterAbi;
				let uAddr = this.$contract.uAddress;
				let uAbi = this.$contract.uAbi;
        let ticketsAddr = this.$contract.ticketsAddress;
        let ticketsAbi = this.$contract.ticketsAbi;
				this.globalData.gMasterContract = await this.globalData.gTronWeb.contract(masterAbi,masterAddr);
        this.globalData.gTicketsContract = await this.globalData.gTronWeb.contract(ticketsAbi,ticketsAddr);
				this.globalData.gUSDTContract = await this.globalData.gTronWeb.contract(uAbi, uAddr);
				// , $onLaunched; ;
				await this.$isResolve();
			},


		}

	};
</script>

<style>
</style>



